@import url("https://fonts.googleapis.com/css?family=Poppins&display=swap");

.pointer-index {
  position: absolute;
  top: 0;
  right: 0;
  height: 150px;
  width: 150px;
}

.pointer {
  opacity: 1;
  position: absolute;
  right: 120px;
  top: 29px;
  font-size: 1.6rem;
  transition: color 4s ease, opacity 0.5s ease;
  animation: fromRight;
  animation-duration: 2s;
  animation-iteration-count: infinite;
}

@keyframes fromRight {
  0% {
    transform: translateX(-20px);
  }

  50% {
    transform: translateX(20px);
  }

  100% {
    transform: translateX(-20px);
  }
}

.menu-btn,
.menu-btn-script {
  position: absolute;
  z-index: 3;
  right: 35px;
  top: 35px;
  cursor: pointer;
  transition: all 0.4s ease-out;
}

.menu-btn .btn-line,
.menu-btn-script .btn-line {
  width: 28px;
  height: 3px;
  margin: 0 0 5px 0;
  background: #fff;
  transition: all 0.2s ease-out;
}

.menu-btn.close,
.menu-btn-script.close {
  transform: rotate(180deg);
}

.menu-btn.close .btn-line:nth-child(1),
.menu-btn-script.close .btn-line:nth-child(1) {
  transform: rotate(45deg) translate(5px, 5px);
}

.menu-btn.close .btn-line:nth-child(2),
.menu-btn-script.close .btn-line:nth-child(2) {
  opacity: 0;
}

.menu-btn.close .btn-line:nth-child(3),
.menu-btn-script.close .btn-line:nth-child(3) {
  transform: rotate(-45deg) translate(7px, -6px);
}

.menu {
  position: fixed;
  top: 0;
  width: 100%;
  visibility: hidden;
}

.menu.show {
  visibility: visible;
}

.menu-branding {
  display: flex;
  flex-flow: column wrap;
  align-items: center;
  justify-content: center;
  float: left;
  width: 50%;
  height: 100vh;
  overflow: hidden;
  opacity: 0.9;
}

.menu-nav {
  margin: 0;
  background: #04040e;
  list-style: none;
  transform: translate3d(0, -100%, 0);
  transition: all 0.2s ease-out;
  opacity: 0.9;
  display: flex;
  flex-flow: column wrap;
  align-items: center;
  justify-content: center;
  float: left;
  width: 50%;
  height: 100vh;
  overflow: hidden;
}

.menu-nav.show {
  transform: translate3d(0, 0, 0);
}

.menu-branding {
  background: #6b6bd9;
  transition: all 0.2s ease-out;
  transform: translate3d(0, 100%, 0);
}

.menu-branding.show {
  transform: translate3d(0, 0, 0);
}

.menu-branding .portrait {
  width: 250px;
  height: 250px;
  background: url(../img/image4.jpg) center center;
  border-radius: 40%;
  border: solid 2px #ED8609;
}

.menu .nav-item {
  transform: translate3d(600px, 0, 0);
  transition: all 0.2s ease-out;
}

.menu .nav-item.show {
  transform: translate3d(0, 0, 0);
}

.menu .nav-item.current>a {
  color: #ED8609;
}

.menu .nav-link {
  display: inline-block;
  position: relative;
  font-size: 30px;
  text-transform: uppercase;
  padding: 1rem 0;
  font-weight: 700;
  color: #fff;
  text-decoration: none;
  transition: all 0.2s ease-out;
}

.menu .nav-link:hover {
  color: #ED8609;
}

.nav-item:nth-child(1) {
  transition-delay: 0.1s;
}

.nav-item:nth-child(2) {
  transition-delay: 0.2s;
}

.nav-item:nth-child(3) {
  transition-delay: 0.3s;
}

.nav-item:nth-child(4) {
  transition-delay: 0.4s;
}

