    display: flex;
  }

  main#script-work .whole-content .extra-content .specs {
    padding-top: 3rem;
    margin-left: 0;
  }
}

@media screen and (max-width: 900px) {
  main#script-work .whole-content {
    width: 100%;
    padding-left: 1rem;
  }

  main#script-work .whole-content .extra-content img {
    margin: 20px 10px 0px 0px;
  }

  main#script-work .whole-content .extra-content h3 {
    overflow: hidden;
  }
}